
import { setContent } from "./setLang";
import { watch, ref, defineComponent } from "@vue/composition-api";


export default defineComponent({
  name: "Hero",
  props: {
    lang: String,
  },

  setup(props) {
    const content = ref();
    const text = {
      DE: {
        title: "Nazim Eren Yanik",
        subTitle: "Full Stack Web Entwickler",
        text:
          "Hallo! Ich heiße Nazim Eren! Ich wohne in Hamburg seit 2017 und entwickle full stack Web Applikationen. Auf dieser Seite können sie meine Arbeitsbeispiele, Entwicklungsstapel sowie Persönliche Information lesen. Falls Sie eine Frage oder Meinung haben, können Sie sich gerne an mich melden!",
      },
      EN: {
        title: "Nazim Eren Yanik",
        subTitle: "Full Stack Web Developer",
        text:
          "Hi There! I'm Nazım Eren! I live in Hamburg/Germany since 2017 and I develop full stack web applications. Here you can find my web development works & skills along with personal details and collection of texts I've posted. Feel free to contact me about any inqueries you might have!",
      },
      TR: {
        title: "Nazım Eren Yanık",
        subTitle: "Full Stack Web Geliştiricisi",
        text:
          "Merhaba! Benim adım Nazım Eren! 2017'den beri Almanya'nın Hamburg eyaletinde yaşıyorum ve burada full-stack web aplikasyonları geliştiriyorum. Bu sitede tamamladığım projelerimi, yazılım yeteneklerimi ve kişisel detaylarımı görebilirsiniz. İlginizi çeken herhangi birşey varsa benimle iletişime geçmekten çekinmeyin!",
      },
    };

    watch(
      () => props.lang,
      () => {
        content.value = setContent(props.lang, text);
      }
    );

    return { content };
  },
});
