
































































import { reactive, watch, defineComponent } from "@vue/composition-api";
import { setContent } from "./setLang";

export default defineComponent({
  name: "Skills",
  props: {
    lang: String,
  },

  setup(props) {
    type Header = {items: { title: string; text: string; text2: string }}
    const header = reactive<Header>({
      items: {
        title: "",
        text: "",
        text2: "",
      },
    });
    const content = {
      EN: {
        title: "Contact",
        text: "",
        text2:
          "You can send me an email for any queries you might have. You can also check out my Github repositories and see what I code; or read my bio on Linkedin. If you are curious about who am I and what I do, my Instagram profile gives a sneak peek too!",
      },
      DE: {
        title: "Kontakt",
        text: "",
        text2:
          "Sie können mir eine E-Mail für alle Fragen senden, die Sie möglicherweise haben. Sie können auch meine Github-Repositorys überprüfen und sehen, was ich codiere, oder lesen meine Biografie auf Linkedin. Wenn Sie neugierig sind, wer ich bin und was ich mache, gibt mein Instagram-Profil auch einen kleinen Einblick!",
      },
      TR: {
        title: "İletişim",
        text: "",
        text2:
          "Sorularınız için bana bir e-posta gönderebilirsiniz. Ayrıca Github depolarımı kontrol edebilir ve neler kodladığımı görebilir, veya Linkedin'deki biyografimi okuyabilirsiniz. Kim olduğumu ve neler yaptığımı merak ediyorsanız, Instagram profilime bir bakış atabilirsiniz!",
      },
    };

    watch(
      () => props.lang,
      () => {
        header.items = setContent(props.lang, content);
      }
    );

    const socialLink = (e: Event) => {
      const event = e.currentTarget as HTMLInputElement;
      if (event.name == "instagram") {
        window.open("http://www.instagram.com/nezurian");
      } else if (event.name == "linkedin") {
        window.open("https://www.linkedin.com/in/nazimerenyanik/");
      } else if (event.name == "github") {
        window.open("https://www.github.com/nezurian");
      } else if (event.name == "mail") {
        window.location.href = "mailto:nazimerenyanik@gmail.com";
      }
    };
    return { socialLink, header };
  },
});
