




















































































































import { reactive, watch, defineComponent } from "@vue/composition-api";
import { setContent } from "./setLang";

export default defineComponent({
  name: "Skills",
  props: {lang: String},
  setup(props) {
    type Header = {items: { title: string; fEnd: string; bEnd: string; pLang: string }}
    const header = reactive<Header>({
      items: {
        title: "",
        fEnd: "",
        bEnd: "",
        pLang: "",
      },
    });

    const titleList = {
      TR: {
        title: "Yazılım Araçları, Kütüphaneler & Diller",
        fEnd: "Front End Araçları",
        bEnd: "Back End Araçları",
        pLang: "Programlama Dilleri",
      },
      DE: {
        title: "Entwicklungsinstrumente, Bibliotheken & Sprachen",
        fEnd: "Front End Instrumente",
        bEnd: "Back End Instrumente",
        pLang: "Programmiersprache",
      },
      EN: {
        title: "Development Tools, Libraries & Languages",
        fEnd: "Front End Tools",
        bEnd: "Back End Tools",
        pLang: "Programming Languages",
      },
    };
    watch(
      () => props.lang,
      () => {
        header.items = setContent(props.lang, titleList);
      }
    );

    const pLang = [
      {
        name: "TypeScript",
        svg: require(`../assets/Icons/TS.svg`),
      },
      {
        name: "JavaScript",
        svg: require(`../assets/Icons/JS.svg`),
      },

      {
        name: "Python",
        svg: require(`../assets/Icons/PYTHON.svg`),
      },
    ];
    const fEnd = [
      {
        name: "React.js",
        svg: require(`../assets/Icons/REACT.svg`),
      },
      {
        name: "Vue.js",
        svg: require(`../assets/Icons/VUE.svg`),
      },
      {
        name: "Tailwind CSS",
        svg: require(`../assets/Icons/TAILWIND.svg`),
      },
      {
        name: "Bootstrap",
        svg: require(`../assets/Icons/BOOTSTRAP.svg`),
      },
      {
        name: "JQuery",
        svg: require(`../assets/Icons/jquery.svg`),
      },
    ];
    const bEnd = [
      {
        name: "Node.js",
        svg: require(`../assets/Icons/NODE.svg`),
      },
      { name: "Express.js", svg: require(`../assets/Icons/NODE.svg`) },
      { name: "Django", svg: require(`../assets/Icons/django.png`) },
      {
        name: "MongoDB",
        svg: require(`../assets/Icons/MongoDB.svg`),
      },
      {
        name: "SQLite",
        svg: require(`../assets/Icons/SQLITE.svg`),
      },
    ];
    return { pLang, bEnd, fEnd, header };
  },
});
