import { AnyObject } from '@vue/composition-api/dist/types/basic';

const setLang = (
  lang: string | unknown,
  data: { DE: string[]; TR: string[]; EN: string[] }
) => {
  let container = [];

  if (lang === "EN") {
    container = [...data.EN];
  } else if (lang === "DE") {
    container = [...data.DE];
  } else container = [...data.TR];
  return container;
};

const setContent = (
  lang: string | unknown,
  data: { EN: {}; DE: {}; TR: {} }
) => {
  let container: any = {};

  if (lang === "EN") {
    container = data.EN;
  } else if (lang === "DE") {
    container = data.DE;
  } else container = data.TR;
  return container;
};

export { setLang, setContent };
