










































































import {
  reactive,
  onMounted,
  watch,
  defineComponent,
} from "@vue/composition-api";
import { setContent } from "./setLang";

export default defineComponent({
  name: "Portfolio",
  props: { lang: String },

  setup(props) {
    const header: { title: string } = reactive({ title: "" });
    const titleList = {
      TR: { title: "Örnek Çalışmalar" },
      DE: { title: "Arbeitsbeispiele" },
      EN: { title: "Work Samples" },
    };
    watch(
      () => props.lang,
      () => {
        header.title = setContent(props.lang, titleList).title;
      }
    );

    const items: {
      name: string;
      img: NodeRequire;
      explanation: string;
    }[] = [
      {
        name: "NE 30.9",
        img: require(`../assets/Portfolio/Personal V1.png`),
        explanation:
          "This is my first personal project, portraying me in a fashion that resembles a software. The whole concept is based on NE (Short for Nazım Eren) and it's core functionalities which helps software development. The page was initially built as a full-stack project with Django back-end, then the need for a back-end server was not substantiated. The project become a front-end project with HTML and Foundation 6 without any front-end frameworks.",
      },
      {
        name: "Nazım Eren Yanık",
        img: require(`../assets/Portfolio/Personal V2.png`),
        explanation:
          "This page is my current personal web application. It is a single page web application that aims to be short, compact and to the point. It is front-end project that solely uses TypeScript and Vue.js with Composition API. The custom styling and responsive design are done with Tailwind CSS. Given Vue.js's and Tailwind CSS's tree-shaking capabilities along, the complete build of this project is dramatically small, making it an ideal showcase solution.",
      },
      {
        name: "Design 45",
        img: require(`../assets/Portfolio/Design45.png`),
        explanation:
          "Design 45 is a wood-working company based in Ankara/Turkey. The application is designed by Elif İşlek by Basicworks, the application development is done by me. This project is completely developed with React.js and Tailwind CSS. The 'projects' section of the application is designed in a way that the project listings are dynamically rendered. ",
      },
      {
        name: "Design 45 Admin",
        img: require(`../assets/Portfolio/Design45Admin.png`),
        explanation:
          "This is a full-stack REST API for Design 45, a wood-working company based in Ankara/Turkey. This application is only used as a control panel for the customer page, where the admin can control the content of the application. It is developed with MERN Stack and gives complete control of the projects and content of the showcase to the administration. It is designed as a single-page-application and a minimalistic design with Tailwind CSS, therefore the build size is exceptionally small.",
      },
    ];

    const showPortfolio: {
      toShow: object[];
      start: number;
      end: number;
    } = reactive({
      toShow: [],
      start: 0,
      end: 3,
    });

    const handleSize = () => {
      const width = window.innerWidth;
      if (width >= 0 && width <= 639) {
        showPortfolio.end = 1;
      }
      if (width >= 640 && width <= 767) {
        showPortfolio.end = 2;
      }
      if (width >= 768 && width <= 1023) {
        showPortfolio.end = 2;
      }
      if (width >= 1024 && width <= 1279) {
        showPortfolio.end = 3;
      }
      if (width >= 1280 && width <= 9999) {
        showPortfolio.end = 3;
      }
    };

    onMounted(() => {
      handleSize();
      const toShow: object[] = items.slice(
        showPortfolio.start,
        showPortfolio.end
      );
      showPortfolio.toShow = toShow;
    });

    const selectPortfolio = (e: Event) => {
      const event = e.currentTarget as HTMLInputElement;
      if (event.name == "forward") {
        if (showPortfolio.end !== items.length) {
          showPortfolio.start += 1;
          showPortfolio.end += 1;
          const newArr = items.slice(showPortfolio.start, showPortfolio.end);
          showPortfolio.toShow = newArr;
        }
      } else if (event.name == "back") {
        if (showPortfolio.start !== 0) {
          showPortfolio.start -= 1;
          showPortfolio.end -= 1;
          const newArr = items.slice(showPortfolio.start, showPortfolio.end);
          showPortfolio.toShow = newArr;
        }
      }
    };
    type ShowProject = {
      show: boolean;
      project: { name: string; img: NodeRequire | string; explanation: string };
    };
    const showProject = reactive<ShowProject>({
      show: false,
      project: { name: "", img: "", explanation: "" },
    });
    const selectProject = (e: Event) => {
      const target = e.currentTarget as HTMLInputElement;
      const project: string = target.name;
      showProject.show = true;

      const filter = items.find((item) => item.name === project);
      if (filter !== undefined) {
        showProject.project = filter;
      }
    };

    const back = () => {
      showProject.show = false;
    };

    return {
      items,
      showPortfolio,
      selectPortfolio,
      selectProject,
      showProject,
      back,
      header,
    };
  },
});
