



















import Navbar from "./components/Navbar.vue";
import About from "./components/About.vue";
import Hero from "./components/Hero.vue";
import Skills from "./components/Skills.vue";
import Portfolio from "./components/Portfolio.vue";
import Contact from "./components/Contact.vue";
import { ref, watch, reactive } from "@vue/composition-api";
import { setContent } from "./components/setLang";

export default {
  name: "App",
  components: {
    Navbar,
    Hero,
    About,
    Skills,
    Portfolio,
    Contact,
  },
  setup() {
    const lang= ref<string>("EN");
    type NavNames = {
      items: {
        About: string;
        Skills: string;
        Portfolio: string;
        Contact: string;
      };
    };
    const navNames = reactive<NavNames>({
      items: {
        About: "",
        Skills: "",
        Portfolio: "",
        Contact: "",
      },
    });
    const navNameList = {
      DE: {
        About: "Über",
        Skills: "Entwicklungsstapel",
        Portfolio: "Arbeitsbeispiele",
        Contact: "Kontakt",
      },
      EN: {
        About: "About",
        Skills: "Skills",
        Portfolio: "Works",
        Contact: "Contact",
      },
      TR: {
        About: "Hakkında",
        Skills: "Yetenekler",
        Portfolio: "Portfolyo",
        Contact: "İletişim",
      },
    };

    watch(
      () => lang.value,
      () => {
        navNames.items = setContent(lang.value, navNameList);
      }
    );

    const chooseLang = (e: Event) => {
      const button = e.currentTarget as HTMLInputElement;
      const target = button.name;
      target == "EN"
        ? (lang.value = "EN")
        : target == "DE"
        ? (lang.value = "DE")
        : target == "TR"
        ? (lang.value = "TR")
        : null;
    };

    const navigation = (e: Event) => {
      const target = e.currentTarget as HTMLInputElement;
      const section = target.name;
      const element = document.getElementById(section);
      if (element !== null) {
        element.scrollIntoView({ behavior: "smooth" });
      } else console.log("Element is null");
    };

    return { navigation, lang, chooseLang, navNames };
  },
};
