
import { watch, ref, defineComponent } from "@vue/composition-api";
import { setLang } from "./setLang";

export default defineComponent({
  name: "Navbar",

  props: {
    lang: String,
    chooseLang: String,
    navigation: String,
  },

  setup(props) {
    const menu = ref(false);
    type Links = string[];
    const Links = ref<Links>([]);

    const NavLinks = {
      EN: ["About", "Works", "Skills", "Contact"],
      DE: ["Über", "Arbeitsbeispiele", "Entwicklungsstapel", "Kontakt"],
      TR: ["Hakkında", "Portfolyo", "Yetenekler", "İletişim"],
    };

    watch(
      () => props.lang,
      () => {
        Links.value = setLang(props.lang, NavLinks);
      }
    );

    const Languages = ["EN", "DE", "TR"];

    const menuBTN = () => {
      if (menu.value === false) {
        menu.value = true;
      } else {
        menu.value = false;
      }
    };

    return { menuBTN, menu, NavLinks, Languages, Links };
  },
});
