
import { reactive, watch, defineComponent } from "@vue/composition-api";
import { setContent } from "./setLang";

export default defineComponent({
  name: "Skills",
  props: {
    lang: String,
  },

  setup(props) {
    type Header = { items: { title: string; text: string } };
    const header = reactive<Header>({
      items: {
        title: "",
        text: "",
      },
    });

    const content = {
      TR: {
        title: "Ben Kimim?",
        text:
          "İstanbul'da doğdum ve büyüdüm. En üst kademe okullara ve üniversitelere gittim ve sosyal bilimci olarak mezun oldum. Kapsamım medyanın sayısallaştırılması, yani 'yeni medya' olmuştur. Zamanla web teknolojileri ve geliştirmeye daha fazla ilgi duydum. 2017'nin sonlarında Almanya'ya taşındıktan sonra, bu teknolojilere olan ilgimi artırmaya başladım ve kullanıcı merkezli web uygulamaları geliştirebileceğim yeni medyanın bir parçası olmaya karar verdim.",
      },
      EN: {
        title: "Who am I?",
        text:
          "I have born and raised in Istanbul/Turkey. I have attended top tier schools and universities and graduated as a social scientist. My scope has been on digitisation of media, i.e, 'new media'. In time I have developed a stronger interest on web technologies and development. After I moved to Germany in late 2017, I have started to expand my interest on such technologies and decided to be a part of the new media, where I can develop user-centered web applications.",
      },
      DE: {
        title: "Wer bin ich?",
        text:
          "Ich bin in Istanbul / Türkei geboren und aufgewachsen. Ich habe erstklassige Schulen und Universitäten besucht und meinen Abschluss als Sozialwissenschaftler gemacht. Mein Schwerpunkt lag auf der Digitalisierung von Medien, d. H. 'Neuen Medien'. Mit der Zeit habe ich ein stärkeres Interesse an Webtechnologien und -entwicklung entwickelt. Nachdem ich Ende 2017 nach Deutschland gezogen bin, habe ich begonnen, mein Interesse an solchen Technologien zu erweitern, und mich entschlossen, Teil der neuen Medien zu sein, in denen ich benutzerzentrierte Webanwendungen entwickeln kann.",
      },
    };

    watch(
      () => props.lang,
      () => {
        header.items = setContent(props.lang, content);
      }
    );

    return { header };
  },
});
